@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
}

$animationSpeed: 15s;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-50vw * 3))}
}


// Styling
.slider {
    background: #F2F2F2;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	height: 100px;
	margin: auto;
	overflow:hidden;
	position: relative;
	width: 100vw;
    

	&::before,
	&::after {
		@include white-gradient;
		content: "";
		height: 100px;
		position: absolute;
		width: 50vw;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(50vw * 6);
       
	}
	
	.slide {
		height: 100px;
		width: 50vw;
        display: flex;
        justify-content: center;
        align-items: center;
        img{
            min-width: 30vw;
            max-width: 30vw;
        }
        .img1 {
            min-width: 20vw;
            max-width: 20vw;
        }
	}
}





















